import React from 'react';
import { render } from "react-dom"
import LeadAuditComponent from '../component/LeadAuditComponent';
import FlowContainer from "../../common/container/core/FlowContainer"
import { Card } from 'react-bootstrap'
import { Button } from 'react-bootstrap'
import { viewLead } from '../../../actions/lead_actions';
import { approveKYC } from '../../../actions/kyc_actions';
import { updateLeadStatus } from '../../../actions/kyc_actions';
import {Redirect} from "react-router-dom";
import {check_priv} from "../../../helpers/storage_helper";
import {dd_value} from "../../../actions/common_actions";


class LeadAuditContainer extends FlowContainer
{
    state = { lead: {},
              to_edit: false }

    componentWillMount()
    {
        this.id = this.props.match.params.id;
        
        viewLead(this.req({id: this.id}))
        .then((response) => 
        {
            if(!response){return };
            if(response.status === "success")
            {
                this.setState({lead: response.data})
            }
        })
        
    }

    handleAudit = (event) => {

        event.preventDefault();
        var borrower = this.state.lead.cust_reg_json;
        borrower.lead_id = this.state.lead.id;
        borrower.flow_rel_mgr_id = this.state.lead.flow_rel_mgr_id;
        borrower.product_group = this.state.lead.product_group;
        approveKYC(this.req({lead_id: this.state.lead.id}, true))
        .then((response) => 
        {
            if(!response){return};
            if(response.status === "success")
            {
              this.setState({cust_id: response.data});
              this.setState({toList: true});
                alert("New Customer created successfully");
                window.open(`/borrower/indiv/view/${this.state.lead.cust_reg_json.cust_id}`, "_blank")
                this.setState({to_edit: true})
            }
            else
            {  
                alert(response.message + "\n" + JSON.stringify(response.data));  
            }
        })
    }
    handleKYCEdit = (event) =>{
        updateLeadStatus(this.req({lead_id: this.id}))
        .then((response) => {
            if(!response){return };
            if(response.status === "success")
            {
                alert(response.message);
                this.setState({to_edit: true})
            }

        })
    }
       
    render()
    { 
        if(!check_priv("lead","audit_kyc")){
            return <Redirect to='/'/>
        }
        return(<>
            {this.state.to_edit ?
            <Redirect to={`/lead/edit/${this.props.match.params.id}`}/> :
            
            <div className="container " style = {{maxWidth :"1500px"}}>
                <Card>
                    <Card.Header style = {{backgroundColor: "#01062c", color: "white"}}>
                        <div className="d-flex justify-content-between">
                            <h4>{this.props.title}</h4>
                            <h4>Status: {dd_value(this.state.lead.status)}</h4>
                        </div>
                    </Card.Header>
                    <Card.Body>
                        <LeadAuditComponent product_group = {this.state.lead.product_group} data = {this.state.lead.cust_reg_json}/>
                        {this.state.lead.status === "50_pending_audit" &&
                        <div className="form-group row reset-margin justify-content-center py-4">
                            <Button className= "btn btn-primary" onClick={this.handleKYCEdit}>Assign to RM</Button>
                            <Button className= "btn btn-primary ml-5" onClick={this.handleAudit}>Approve KYC</Button>
                        </div>
                        }
                           
                    </Card.Body>
                </Card>
            </div>
        }
        </>
        )
    }
}

export default LeadAuditContainer;

