import FlowComponent from '../common/component/core/FlowComponent';
import React from 'react';
import {borrowerSearch} from '../../actions/borrower_actions';
import ReactTable from "react-table"; 
import {format_date_tbl,capitalize_first_letter} from '../../helpers/common_helper';
import { Link} from 'react-router-dom';
import {searchLead} from "../../actions/lead_actions";






class KycPendingList extends FlowComponent {

	state = {
		columns :[],
		list : []
	}

	componentWillMount() {
		let lead = {}
		lead.status = '50_pending_audit'
      	searchLead(this.req(lead))
		        .then((response) => {
		        	if(!response){return };
		        	if(response.status === "success"){
		        		let columns 
		        		columns = [
						// 	{
		        		// 	Header : "Cust ID",
		        		// 	accessor: 'cust_id' ,
		        		// 	Cell: e =>  <Link to={"/borrower/indiv/view/"+e.value} target="_blank" >{e.value}</Link>,
						//
		        		// },
		        		{
		        			Header : "Biz Name",
		        			id: 'biz_name', 
		        			accessor: 'biz_name',
		        		},
						{
		        			Header : "Reg Date",
		        			id: 'date', 
		        			accessor:row => format_date_tbl(row.lead_date),
		        		},
		        		{
		        			Header : "Acc Prvdr Code",
		        			accessor: 'acc_prvdr_code' ,
		        		},
		        		{
		        			Header : "Account Number",
		        			accessor: 'acc_number' ,
		        		},
		        		{
		        			Header : "RM Name",
		        			accessor: 'flow_rel_mgr_name' ,
		        		},
		        		
		        		{
		        			Header : "Action",
							accessor: 'id' ,
		        			Cell: e =>  <Link to={"lead/audit_kyc/"+ e.value} target="_blank" >Audit KYC</Link>,

		        		}

		        		]

		        		this.setState({columns,lists:response.data})
		        	}



		        })
		
    }



    render(){
        return (

        	<div>
				<div className="card">
					<div className="card-header card-header-icon card-header-primary">
						<h4 className="card-title text-white d-inline-block">Leads with Pending KYC Audit</h4>
					</div>
					<div className="card-body">
						<div className="table-responsive">
				<ReactTable  minRows = {2}  data={this.state.lists}  columns={this.state.columns}  defaultSorted={[{id: "date",
              desc: true}]}  />
        	</div>
					</div>
				</div>
			</div>
        	)

    }
}


export default KycPendingList;
